/* Header and Table Footer Styling */
.App-Header {
  border-bottom: 2px solid goldenrod;
}

.Header-Main {
  height: 125px;
  margin: 0 18vw;
  display: flex;
  justify-content: space-between;
}

.Header-Nav {
  display: flex;
}
.Subheader-Nav, .Footer-Nav {
  margin: 0;
  padding: 0 18vw;
  display: flex;
  background-color: #FAFAFA;
}
.Subheader-Nav {
  justify-content: center;
}
.Footer-Nav {
  justify-content: space-around;
}

.Header-Nav-Dropdown {
  position: fixed;
  top: 125px;
  background-color: #F5F5F5;
}

.Header-Link {
  padding: 5px 10px;
  font-size: 1.75em;
  text-decoration: unset;
  color: #000;
  background-color: transparent;
  border: none;
}
.Header-Nav-Dropdown-Link {
  padding: 5px 10px;
  font-size: 1.5em;
  color: #000;
  text-decoration: unset;
}
.Subheader-Nav-Link, .Footer-Nav-Link {
  padding: 5px 10px;
  color: darkblue;
  background-color: transparent;
  border: none;
  list-style: none;
}

.Header-Link:hover {
  color: goldenrod;
}
.Subheader-Nav-Link:hover, .Footer-Nav-Link:hover {
  color: dimgray;
  cursor: pointer;
}

.Header-Link-Active {
  color: dimgray;
}
.Subheader-Nav-Link-Active {
  color: goldenrod;
}

.Subheader-Label, .Footer-Label {
  display: flex;
}
.Subheader-Select, .Footer-Input {
  width: 45px;
  margin: 0 5px;
  text-align: center;
}
.Subheader:focus, .Footer-Input:focus {
  border: none;
}


/* Cards Component Styling */
.App-Cards {
  padding: 0 15vw;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
}

.Card {
  margin: 20px;
  min-width: 250px;
  height: 100px;
  background-color: #eaeaea;
  border-radius: 10px;
}

.Card:hover {
  cursor: pointer;
}

/* Table Component Styling */
table {
  padding: 0 15vw;
  width: 100vw;
  min-height: 70vh;
  background-color: #eaeaea;
}

th, td {
  padding: 3px 10px;
  vertical-align: top;
  text-align: left;
  font-size: 0.8em;
}